.container {
  .header {
    background: #1b1e24;
    width: fit-content;
    border-top-left-radius: 4px;
    border-top-right-radius: 4px;
    padding: 4px 10px;

    &.isDraggingOver {
      background: #444a55;
    }
  }

  .list {
    padding: 6px 10px;
    background: #1b1e24;

    &.isDraggingOver {
      background: #444a55;
    }
  }
}